<template>
  <v-data-table
    :headers="headers"
    :items="categories"
    sort-by="calories"
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title>{{ $route.name }}</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="purple" dark class="mb-2" v-bind="attrs" v-on="on">
              <v-icon right dark left class="mr-1">
                mdi-plus
              </v-icon>
              New Category
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-form v-model="isValid">
                <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="12"
                    md="12"
                  >
                    <v-text-field
                      v-model="editedItem.name"
                      label="Category name"
                      aria-required="true"
                      :rules="[v => !!v || 'Name is required']"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="12"
                    md="12"
                  >
                    <v-text-field
                      v-model="editedItem.thumbnail"
                      label="Thumbnail URL (optional)"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
              </v-form>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="purple darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="purple darken-1"
                text
                @click="save"
                :disabled="!isValid"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="purple darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="purple darken-1" text @click="deleteItemConfirm">OK</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.thumbnail="{ item }">
      <div class="pa-2">
        <v-img :src="item.thumbnail ? item.thumbnail : placeholderImage" :alt="item.name" height="60px" width="80px"></v-img>
      </div>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>

<script>
  import CategoryService from "../Services/CategoryService";
  export default {
    name: "QuoteFilter",
    data: () => ({
      dialog: false,
      isValid: true,
      placeholderImage: "https://via.placeholder.com/80x60/9400D3/FFFFFF?text=QStatus",
      dialogDelete: false,
      headers: [
        { text: 'Thumbnail', align: 'start', sortable: false, value: 'thumbnail' },
        { text: 'Name', align: 'start', sortable: false, value: 'name' },
        { text: 'Created At', value: 'createdAt' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      categories: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        thumbnail: '',
      },
      defaultItem: {
        name: '',
        thumbnail: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Category' : 'Edit Category'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      async initialize () {
        this.categories = await CategoryService.getCategories();
      },

      editItem (item) {
        this.editedIndex = this.categories.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.categories.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.categories.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      async save () {
        this.editedItem.createdAt = new Date();
        if (this.editedIndex > -1) {
          await CategoryService.updateCategory(this.editedItem._id, this.editedItem);
          Object.assign(this.categories[this.editedIndex], this.editedItem);
        } else {
          await CategoryService.createCategory(this.editedItem);
          this.categories.push(this.editedItem);
        }
        this.close()
      },
    },
  }
</script>

